/* NAVIGATION SECTION */

/* Styling for navbar toggler icon in dark mode */
.navbar-dark .navbar-toggler {
  border-color: rgba(255, 255, 255, 0.1);
}

.navbar-dark .navbar-toggler-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30' width='30' height='30' fill='%23fff'%3e%3cpath d='M4 7h22v3H4zM4 15h22v3H4zM4 23h22v3H4z'/%3e%3c/svg%3e");
}
/* Set background color and text color for body element */
body {
  background-color: var(--bg-color);
  color: var(--text-color);
}
/* Styling for navigation link highlight when hovered over or focused on */
.nav-link-highlight:hover,
.nav-link-highlight:focus {
  color: rgb(255, 255, 255);
  text-decoration: none;
  background-color: transparent;
}

.navbar-dark .navbar-nav .nav-link {
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.navbar-brand {
  font-family: Georgia, 'Times New Roman', Times, serif;
}
/* HEADER SECTION */
.bg-dark {
  background-attachment: fixed;
  background-image: linear-gradient(45deg, #F77CEC 25%, #F79C59 70%);
  background-position: 0% 0%;
  background-repeat: repeat;
  background-size: auto;
  }
  
  /* CSS for Old Devices Smaller than 320px (mobile) */
  @media (max-width: 320px) {
    .text-white.text-decoration-none {
      font-size: 24px;
    }
  
    .fa-code {
      font-size: 20px;
    }
  }
  
  /* CSS for screens between 321px and 575px (mobile) */
  @media (min-width: 321px) and (max-width: 575px) {
    .text-white.text-decoration-none {
      font-size: 37px;
    }
  
    .fa-code {
      font-size: 33px;
    }
  }
  
  /* CSS for screens between 576px and 992px (tablet) */
  @media (min-width: 576px) and (max-width: 992px) {
    .text-white.text-decoration-none {
      font-size: 58px;
    }
  
    .fa-code {
      font-size: 54px;
    }
  }
  
  /* CSS for screens greater than 992px (desktop) */
  @media (min-width: 993px) {
    .text-white.text-decoration-none {
      font-size: 80px;
    }
  
    .fa-code {
      font-size: 76px;
    }
  }
  
  .fa-code {
    margin-bottom: 10px;
    margin-top: 10px;
  }
  
  .text-white.text-decoration-none {
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 0;
    padding: 10px;
    font-family: Georgia, 'Times New Roman', Times, serif;
  }
  
/* ABOUT SECTION */

/* Styling for the heading */
.about-heading {
  text-align: center;
  margin-top: 0.4rem;
  margin-bottom: 3rem;
  font-size: 50px;
}
/* Styling for the image */
.about-image {
  max-width: 100%; /* Ensure the image scales correctly */
  width: 200px;
  height: auto;
  margin-bottom: 20px; /* Add margin-bottom for mobile view */
}

/* Font styling for text */
.card-text {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.6;
  text-align: justify;
  text-justify: inter-word;
}

/* Font styling for header */
.card-title {
  font-family: "Arial", sans-serif;
  font-size: 36px;
  font-weight: bold;
  text-align: center;
}
/* Styling for the about section container */
#about {
  margin-bottom: 12rem;
}

/* Tablet view */
@media (min-width: 768px) {
  .about-image {
    width: 250px;
    margin-bottom: 0;
  }
}

/* Desktop view */
@media (min-width: 992px) {
  .about-image {
    width: 300px;
  }
}

