/* APP SECTION */

/* Set default font for HTML element and line height */
html {
  font-family: "Arial", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  line-height: 1.6;
}
/* Set height, margin, and background image for body element */
body {
  height: 100%;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(
    to top right,
    #fac90624 0%,
    #ff660014 50%,
    #cc330025 100%
  );
  background-repeat: no-repeat;
  background-attachment: fixed;
  min-height: 100vh;
}
/* Set display and flex direction for root element */
#root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
/* Set display, flex direction, and flex for app element */
.app {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
/* Set flex grow and padding for main container element */
.main-container {
  flex-grow: 1;
  padding-top: 5em;
}
/* Set color, padding, and text alignment for footer element */
.footer {
  color: #fff;
  text-align: center;
}
/* Set display, flex direction, and wrap for flex container element */
.flex-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
/* Set font for code elements */
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* INDEX SECTION */

/* Hide scrollbar for Chrome, Safari, and Opera */
*::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for Internet Explorer and Edge */
* {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

